<section class="statistics" id="statistics"><div class="container"><div class="statistics-wrap"><h3 class="statistics__title">{{ statistics.title }}</h3><h5 class="statistics__subtitle">{{ statistics.subtitle }}</h5><ul class="statistics__info"><li class="statistics__info-item" v-for="item in statistics.info" :key="item"><span>{{ item.number }}</span><p>{{ item.text }}</p></li></ul></div></div></section>