<section class="sidebar" id="sidebar"><div class="container"><div class="sidebar-wrap"><div class="sidebar__logo"><h2 class="sidebar__logo-title">{{ sidebar.title }}</h2></div><div class="sidebar__menu"><ul class="sidebar__menu-list"><li class="sidebar__menu-list-item" v-for="item in sidebar.menu"><router-link :to="item.link" @click="scrollTop(), sidebarRem()"><component :is="item.src"></component><h3 class="sidebar__menu-list-text">{{ item.text }}</h3></router-link></li></ul></div><div class="sidebar__form"><button class="sidebar__form-btn" @mousemove="btnLogAnim($event)"><span>{{ sidebar.login }}</span></button><button class="sidebar__form-btn" @mousemove="btnLogAnim($event)"> <span>{{ sidebar.sign }}</span></button></div><button class="sidebar__exit" @click="sidebarUnlock()"></button></div></div></section>