<section class="footer"><div class="container"><div class="footer-wrap"><div class="footer__head"><h2 class="footer__head-logo">{{ footer.logo }}</h2><h4 class="footer__head-subtitle" v-for="item in footer.subtitle" :key="item">{{ item }}</h4></div><div class="footer__list"><div class="footer__list-col" v-for="(list, idx) in footer.list" :key="'footer-list-0' + idx"><h5 class="footer__list-title" v-if="list.title">{{ list.title }}</h5><ul class="footer__list-links" v-if="list.links"><li class="footer__list-links-item" v-for="item in list.links" :key="item"><a :href="item.link">{{ item.text }}</a></li></ul><ul class="footer__list-social" v-if="list.social"><li class="footer__list-social-item" v-for="item in list.social" :key="item"><a :href="item.link"><img :src="require('@/assets/img/' + item.src)"></a></li></ul><div class="footer__list-map" v-if="list.map"><img :src="require('@/assets/img/' + list.map)"></div></div></div></div></div></section>