<section class="header"><div class="container--big"><div class="header-wrap"><div class="header__nav"><div class="header__nav-logo"><router-link class="header__nav-logo-title" to="/">{{ header.logo }}</router-link></div><div class="header__nav-menu"><burger-sidebar :text="header.menu" @action="sidebarUnlock()"></burger-sidebar></div></div><div class="header__body"><h2 class="header__body-title">{{ header.title }}</h2><p class="header__body-text">{{ header.text }}</p><h6 class="header__body-author">{{ header.author }}</h6></div></div></div></section>