<section class="social" id="social"><div class="container--big"><div class="social-wrap"><div class="social__title"><section-title :title="social.title" :subtitle="social.subtitle"></section-title></div><div class="social__case"><router-link class="social__case-img" to="#" v-for="item in social.case" :key="item" :to="item.link"><img :src="require('@/assets/img/' + item.src)" :alt="item.alt"><span>Open</span></router-link></div><div class="social__appeal"><h4 class="social__appeal-title">{{ social.appeal }}</h4></div></div></div></section>