<section class="navigation" id="navigation"><div class="container--big"><div class="navigation-wrap"><div class="navigation__logo"><router-link class="navigation__logo-link" to="/">{{ navigation.logo }}</router-link></div><div class="navigation__burger"><burger-sidebar :text="header.menu" @action="sidebarUnlock()"></burger-sidebar></div><div class="navigation__list"><ul class="navigation__list-wrap"><li class="navigation__list-item" v-for="item in navigation.list" :key="item"><router-link class="navigation__list-link" :to="item.link" @click="scrollTop()">{{ item.text }}</router-link></li></ul></div></div></div></section>