<section class="team" id="team"><div class="container"><div class="team-wrap"><div class="team__title"><section-title :title="team.title" :subtitle="team.subtitle"></section-title></div><ul class="team__list"><li class="team__list-item" v-for="item in team.list" :key="item"><div class="team__list-img"><img :src="require('@/assets/img/' + item.src)" :alt="item.alt"></div><h4 class="team__list-name">{{ item.name }}</h4><h6 class="team__list-meta">{{ item.meta }}</h6></li></ul></div></div></section>