<section class="featured" id="featured"><div class="container"><div class="featured-wrap"><div class="featured__title"><section-title :title="featured.title" :subtitle="featured.subtitle"></section-title></div><ul class="featured__cards"><li class="featured__cards-item" v-for="item in featuredCardList" :key="item"><a class="featured__cards-item-wrap" :href="item.link" @mousemove="featuredCardAnim($event)" @mouseout="featuredCardStop($event)"><img class="featured__cards-img" :src="require('@/assets/img/' + item.src)" :alt="item.alt"><h4 class="featured__cards-title">{{ item.title }}</h4><p class="featured__cards-price">{{ item.price }}</p><a class="featured__cards-link" :href="item.link">{{ item.linkText }}</a></a></li></ul></div></div></section>